import React from 'react';
import './styles/meeting.css';
import imgMeet from '../images/logos/logo_reunion.png';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import { EffectCards } from 'swiper';

import meet0 from '../images/meet/Meet00.png';
import meet1 from '../images/meet/Meet01.png';
import meet2 from '../images/meet/Meet02.png';
import meet3 from '../images/meet/Meet03.png';
import meet4 from '../images/meet/Meet04.png';
import meet5 from '../images/meet/Meet05.png';
import meet6 from '../images/meet/Meet06.png';
import meet7 from '../images/meet/Meet07.png';
import meet8 from '../images/meet/Meet08.png';

import finger from '../images/misc/hand.gif';



function Meeting() {
    return ( 
            <div className='meeting__container'>
                <section id='section3' />
                    <div className='meeting__header'>
                        <img src={ imgMeet } alt="Another clan's Logo" />
                    </div>
                    <div className='meeting__title'>
                        Corran los ltimos meses del ao 2016 y el clan ya llevaba 3 aos de su creacin, muchos de nosotros 
                        un poco en broma y otro poco en serio comenzamos a plantearnos que era increble el hecho de compartir 
                        tanto tiempo juntos y no conocernos personalmente, as que slo faltaba la excusa perfecta y esta apareci 
                        a fines de ese mismo ao, cuando Ubisoft (Empresa creadora de Assassins Creed) anunci que su pelcula 
                        se estrenara en cines a fines de 2016... Listo, era el momento de romper la virtualidad.<br/>
                        Esa primer "juntada" real de un puado de caticos que a duras penas llegbamos a la media docena, el cine 
                        y un asado en la casa del lder fue el puntapi inicial de lo que luego dio paso a lo que denominamos 
                        "Juntada Anual de CAOS".<br/>
                        El xito de estas reuniones fue poco a poco en aumento, para el ao 2019 duplicamos el nmero de 
                        participantes de la primera, pero ya para el ao 2020 era prcticamente imposible de organizar dentro de 
                        una casa, as que dimos un gran paso y el evento de ese ao se convirti en todo un hito dentro del clan, 
                        CAOS ya no solamente se destacaba por su longevidad y hermandad, ahora tambin sus reuniones anuales se 
                        convirtieron en culto.<br/>
                        La "Juntada Anual de CAOS 2020" fue especialmente destacable, ya que fue la primera a nivel internacional, 
                        no slo contamos con los miembros de Uruguay si no que se sumaron grandes amigos desde Argentina. Tamao 
                        despliegue necesitaba otra organizacin, pero por sobre todo mas tiempo y logstica para sacarle el jugo a 
                        tanto esfuerzo. Fue as que pensamos alquilar una casa en la playa por todo un fin de semana!!!<br/>
                        Hubo de todo y el tiempo qued muy corto entre charlas, chistes, ancdotas, playa, cartas, ftbol, asados, 
                        alcohol, pero por sobre todo HERMANDAD... lo que nos define!
                    </div>
                    <div className='meeting__pics_container'>
                        <Swiper
                            modules={[EffectCards]}
                            effect={'cards'}
                            grabCursor={true}
                            centeredSlides={true}
                            slidesPerView={'auto'}
                            coverflowEffect={{
                                rotate: 50,
                                stretch: 0,
                                depth: 100,
                                modifier: 1,
                                slideShadows: true,
                                }}
                            className='mySwiper'>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet0} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet1} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet2} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet3} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet4} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet5} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet6} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet7} />
                                    </div>
                                </SwiperSlide>
                                <SwiperSlide className='meet_pic_container'>
                                    <div className='meet_pic_pic'>
                                        <img alt='Foto de Juntada' src={meet8} />
                                    </div>
                                </SwiperSlide>
                                
                        </Swiper>
                    </div>
                    <div className='meet__footer'>
                            <img alt='Desliza!' src={finger} />
                    </div>
            </div>
     );
}

export { Meeting };